"use client";
import { useToursQuery } from "@/hooks/queries/useToursQuery";
import React from "react";
import Container from "./Container";
import Title from "./ui/Title";
import parse from "html-react-parser";
import Button from "./Button";

const ToursList = () => {
  const { data, isLoading, isError } = useToursQuery();

  return (
    <Container>
      <section>COUNTODWN TO MOST RECENT </section>

      <section className="flex flex-col">
        {data &&
          data.map((tourData) => (
            <div className="flex">
              <figure className="w-1/2">
                <img className="h-auto w-full" src={`http://localhost:4444/images/tours/${tourData.image1}`} />
              </figure>
              <section className="w-1/2 border-neutral-200 border-[2px] relative flex justify-center ps-[5%] pe-[10%]  flex-col">
                <Title className="mb-2" text={tourData.title} />
                <div className="opacity-50 text-xl">{parse(tourData.content.split("</p>")[0])}</div>
                <Button className="w-[40%] mt-8 bg-fullwhite">Se mere</Button>
              </section>
            </div>
          ))}
      </section>
    </Container>
  );
};

export default ToursList;
